import React, { Component } from 'react'
import './Notification.css'
import {Link} from 'react-router-dom'
import axios from 'axios'
import authService from '../../services/auth-service';
import { API_URL } from '../../services/url';
export class Notification extends Component {
  constructor(props)
  {
      super(props)
      this.state = {
            userdata : [],
            company_name: "",
            skip: 0,
            limit: 5
      }
      this.nextpagehandler = this.nextpagehandler.bind(this);
      this.prevpagehandler = this.prevpagehandler.bind(this);
      
  }
  prevpagehandler() {
    console.log("paginationhandler");
    // this.setState({
    //   skip: this.state.skip + this.state.limit
    // });\
    this.state.skip = this.state.skip - this.state.limit;
    let limit = this.state.limit;
    let dataskip = this.state.skip;
    console.log(dataskip);
    console.log(`${API_URL}/company/list`, {
      params: { skip: dataskip, limit: limit }
    });
    axios
      .get(
        `${API_URL}/company/list`,
        { params: { skip: dataskip, limit: limit } },
        (axios.defaults.headers.common["w_auth"] = authService.getToken())
      )
      .then(response => {
        console.log(response.data);
        const data = response.data;
        this.setState({ userdata: data });
      })
      .catch(error => {
        console.log(error);
      });
  }
  nextpagehandler() {
    console.log("paginationhandler");
    // this.setState({
    //   skip: this.state.skip + this.state.limit
    // });\
    this.state.skip = this.state.skip + this.state.limit;
    let limit = this.state.limit;
    let dataskip = this.state.skip;
    console.log(dataskip);
    console.log(`${API_URL}/company/list`, {
      params: { skip: dataskip, limit: limit }
    });
    axios
      .get(
        `${API_URL}/company/list`,
        { params: { skip: dataskip, limit: limit } },
        (axios.defaults.headers.common["w_auth"] = authService.getToken())
      )
      .then(response => {
        console.log(response.data);
        const data = response.data;
        this.setState({ userdata: data });
      })
      .catch(error => {
        console.log(error);
      });
  }
  componentDidMount() {
      this.handleclick();
  }
   handleclick() {        
    axios.get(`${API_URL}/requirement/recommendedList`,axios.defaults.headers.common['w_auth'] = authService.getToken())
    .then( async response => {
        console.log(response)
          const data = response.data;
          await this.setState({ userdata : data })
            console.log(this.state.userdata)
        })
  
  }
  

render() {
  let buttondisplay
  // if (this.state.skip == 0) {
  //   buttondisplay = (
  //     <div>
  //       <button
  //         type="button"
  //         className="btn btn-info"
  //         onClick={this.prevpagehandler.bind(this)}
  //         disabled
  //       >
  //         Previous
  //       </button>
  //       <button
  //         type="button"
  //         className="btn btn-info"
  //         onClick={this.nextpagehandler.bind(this)}
  //       >
  //         Next
  //       </button>
  //     </div>
  //   );
  // } else {
  //   buttondisplay = (
  //     <div>
  //       <button
  //         type="button"
  //         className="btn btn-info"
  //         onClick={this.prevpagehandler.bind(this)}
  //       >
  //         Previous
  //       </button>
  //       <button
  //         type="button"
  //         className="btn btn-info"
  //         onClick={this.nextpagehandler.bind(this)}
  //       >
  //         Next
  //       </button>
  //     </div>
  //   );
  // }
  let sr=0;
   let requirementdata;
   if(this.state.userdata.length === 0)
   {
     requirementdata = (
       <div>
         <center><h4><strong>There is No Notification.</strong></h4></center>
         </div>
     )
   }
   else
   {
    if (this.state.skip == 0) {
      buttondisplay = (
        <div>
          <button
            type="button"
            className="btn btn-info"
            onClick={this.prevpagehandler.bind(this)}
            disabled
          >
            Previous
          </button>
          <button
            type="button"
            className="btn btn-info"
            onClick={this.nextpagehandler.bind(this)}
          >
            Next
          </button>
        </div>
      );
    } else {
      buttondisplay = (
        <div>
          <button
            type="button"
            className="btn btn-info"
            onClick={this.prevpagehandler.bind(this)}
          >
            Previous
          </button>
          <button
            type="button"
            className="btn btn-info"
            onClick={this.nextpagehandler.bind(this)}
          >
            Next
          </button>
        </div>
      );
    }
     requirementdata = (
       <div>
          <table className="table table-responsive-sm table-bordered">
   <thead className="thead-dark">
     <tr>
        <th scope="col">Sr. No.</th>
       <th scope="col">Equipment Name</th>
               <th scope="col">Capacity</th>
               <th scope="col">Tenure(Days)</th>
               <th scope="col">City</th>
               <th scope="col">Company Name</th>
       
       <th scope = "col">Contact Person</th>
       <th scope="col">Contact Number</th>
       {/* <th scope="col">E - Mail</th> */}
       
               <th scope="col">Pincode</th>
               <th scope="col">Date Of Creation</th>
       
     </tr>
   </thead>
   <tbody>
   {
           this.state.userdata.length ?
           this.state.userdata.map(function(item, _id) {
             return(
       <tr key = {_id}>
        <td>{++sr}</td>
       <td>{item.subcategory.name || "NO DATA"}</td>
       
                 <td>{item.company ? item.capacity : 'No data'} </td>
                 <td>{ item.company ?item.tenure : 'No data'} </td>
                 <td>{ item.company ?item.location : 'No data'} </td>
                 <td>{item.company ? item.company.name : 'No data'} </td>
                 <td>{item.company ? item.company.contactPerson : 'No data'} </td>
       <td>{ item.company ?item.company.contactNumber : 'No data'} </td>
       {/* <td>{ item.company ?item.company.email : 'No data'} </td> */}
       
                 <td>{item.company ? item.pinCode : 'No data'} </td>
                 <td>{ item.company ?new Date(item.createdAt).toString().split(' ').slice(0, 4).join(' ') : 'No data'} </td>
       
             </tr>
   )
           }, this
   )
           :
           <span>No Data Present....</span>
         }
   </tbody>
 </table>

       </div>
     )
   }
 return (
     <React.Fragment>
   <div> 
         <main className="main">
         <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/"}>Home</Link></li>
          <li class="breadcrumb-item">
            View Notification
          </li>
          </ol>
             <div className="container-fluid">
                 <div className="row">
                    
                     <div className="col-sm-12">
                     <div className="card custom-equipment-card-view">
                 <div className="card-body custom-card-padding">
                {requirementdata}
                
               </div>
               <div className="text-center center-block custom-pagination-btn">
                      {buttondisplay}
                    </div>
             </div>
                     </div>
                 </div>
             </div>
         </main>
   </div>
   </React.Fragment>
 )
}
}
export default Notification
